import React, { useState } from 'react';
import { config, useSpring } from 'react-spring';
import { navigate } from 'gatsby-link';
import Layout from '../components/layout';
import { AnimatedBox, ActionButton } from '../elements';
import SEO from '../components/SEO';
import axios from 'axios';

const About = () => {
  const pageAnimation = useSpring({
    config: config.slow,
    from: { opacity: 0 },
    to: { opacity: 1 },
  });

  const [serverState, setServerState] = useState({
    submitting: false,
    status: null,
  });
  const handleServerResponse = (ok, msg, form) => {
    setServerState({
      submitting: false,
      status: { ok, msg },
    });
    if (ok) {
      form.reset();
    }
  };
  const handleOnSubmit = (e) => {
    e.preventDefault();
    const form = e.target;
    setServerState({ submitting: true });
    axios({
      method: 'post',
      url: 'https://getform.io/f/19d1815d-8026-4cc2-9ebf-6b48aab21463',
      data: new FormData(form),
    })
      .then(() => navigate(form.getAttribute('action')))
      /*.then((r) => {
          
        handleServerResponse(true, 'Thanks!', form);
      })*/
      .catch((r) => {
        handleServerResponse(false, r.response.data.error, form);
      });
  };

  return (
    <Layout color="#000">
      <SEO
        title="Contact Us | Bhavisha Patel"
        desc="Contact Bhavisha Patel - Graphic Designer"
      />
      <AnimatedBox
        style={pageAnimation}
        py={[6, 6, 6, 8]}
        px={[6, 6, 8, 6, 8, 13]}
      >
        <h1>Let's Connect.</h1>
        <p>
          I would be happy to work with your business or agency to help with any
          of your design, advertising or production needs.
        </p>

        <section>
          <div className="contact-grid-container">
            <div className="contact-grid-item text-left">
              <a
                href="mailto:bhavishampatel@gmail.com"
                title="Email Bhavisha"
                aria-label="email Bhavisha"
              >
                <img
                  src="/images/email.svg"
                  alt="Email"
                  className="contact-icon"
                />
              </a>
            </div>
            <div className="contact-grid-item text-left">
              <a
                href="tel:647-533-7473"
                title="Call Bhavisha"
                aria-label="call Bhavisha"
              >
                <img
                  src="/images/phone.svg"
                  alt="Phone"
                  className="contact-icon"
                />
              </a>
            </div>
            <div className="contact-grid-item text-left">
              <a
                href="https://ca.linkedin.com/in/bhavishampatel"
                target="_blank"
                title="Tweet Bhavisha"
                aria-label="tweet Bhavisha"
              >
                <img
                  src="/images/linkedin.svg"
                  alt="Linkedin Profile Bhavisha"
                  className="contact-icon"
                />
              </a>
            </div>
          </div>
        </section>
        <section>
          <p style={{ paddingTop: '48px' }}>I'd love to hear from you.</p>
          <div>
            <div>
              <section>
                <div className="confirmation-message">
                  {serverState?.status && (
                    <p className={!serverState.status.ok ? 'errorMsg' : ''}>
                      {serverState.status.msg}
                    </p>
                  )}
                </div>
                <form
                  className="contact-form"
                  onSubmit={handleOnSubmit}
                  action="/thanks"
                >
                  <div className="fields">
                    <div className="field half">
                      <input
                        type="text"
                        name="name"
                        id="name"
                        required={true}
                        placeholder="Name"
                      />
                    </div>
                    <div className="field half">
                      <input
                        type="email"
                        name="email"
                        id="email"
                        required={true}
                        placeholder="Email"
                      />
                    </div>
                    <div className="field">
                      <textarea
                        className="message"
                        name="message"
                        id="message"
                        required={true}
                        placeholder="Message"
                        rows="4"
                      />
                    </div>
                  </div>

                  <ul className="actions">
                    <li>
                      <ActionButton
                        type="submit"
                        color="#ddd"
                        width="6em"
                        className="primary"
                        disabled={serverState.submitting}
                      >
                        Send
                      </ActionButton>
                    </li>
                    <li>
                      <ActionButton
                        className="ml-8"
                        color="#ddd"
                        width="6em"
                        type="reset"
                      >
                        Reset
                      </ActionButton>
                    </li>
                  </ul>
                </form>
              </section>
            </div>
          </div>
        </section>
      </AnimatedBox>
    </Layout>
  );
};

export default About;
